    },
    mounted() {
        this.horairesSelect = [-1];
        this.directionSelect = [-1];
        this.actualPage = 'Postes'
    },
    methods: {
        // handleTabChange() {
        //     this.read = true;
        // },
        togglePage(page) {
            console.log('page ',page)
            this.actualPage = page
            this.tableKey++
        },
        EditLine() {
            this.isLoading = true;
            if (this.dateSelect.length == 0) {
                this.form.date = this.form.date;
            } else {
                this.form.date = JSON.stringify(this.dateSelect);
            }
            this.selectedDays.forEach((element) => {
                switch (element) {
                    case "Lundi":
                        this.form.lun = true;

                        break;

                    case "Mardi":
                        this.form.mar = true;

                        break;
                    case "Mercredi":
                        this.form.mer = true;
                        break;

                    case "Jeudi":
                        this.form.jeu = true;
                        break;
                    case "Vendredi":
                        this.form.ven = true;
                        break;

                    case "Samedi":
                        this.form.sam = true;

                        break;
                    case "Dimanche":
                        this.form.dim = true;
                        break;
                }
            });
            // this.form.postes = this.horairesSelect.join(",");
            this.form.horaires = this.horairesSelect.join(",");
            this.form.directions = this.directionSelect.join(",");
            this.form.userFiltre = JSON.stringify(this.form.userFiltre);
            // this.form.postesbaladeur = this.form.postesbaladeur_id
            // console.log('this.form',this.form,);
            this.axios
                .post(
                    "/api/modelslistings/" + this.form.id + "/update",
                    this.form
                )
                .then((response) => {
                    this.isLoading = false;
                    this.gridApi.applyServerSideTransaction({
                        update: [response.data],
                    });
                    this.$bvModal.hide(this.modalFormId);
                    this.$toast.success("Operation effectuer avec succes");
                    // this.$emit("close");
                    console.log(response.data);
                })
                .catch((error) => {
                    this.errors = error.response.data.errors;
                    this.isLoading = false;
                    this.$toast.error(
                        "Erreur survenue lors de l'enregistrement"
                    );
                });
        },
        DeleteLine() {
            this.isLoading = true;
            this.axios
                .post("/api/modelslistings/" + this.form.id + "/delete")
                .then((response) => {
                    this.isLoading = false;

                    this.gridApi.applyServerSideTransaction({
                        remove: [this.form],
                    });
                    this.gridApi.refreshServerSide();
                    this.$bvModal.hide(this.modalFormId);
                    this.$toast.success("Operation effectuer avec succes");
                    this.$emit("close");
                    console.log(response.data);
                })
                .catch((error) => {
                    console.log(error.response.data);
                    this.isLoading = false;
                    this.$toast.error("Erreur survenue lors de la suppression");
                });
        },
        onGridReady(params) {
            console.log("on demarre", params);
            this.gridApi1 = params.api;
            this.columnApi = params.columnApi;
            this.isLoading = false;
            this.calendarKey++; // Incrémente la valeur de calendarKey pour forcer le rendu du composant <FullCalendar>
        },
        openCreate(data) {
            if (data == 'directions') {
                this.showForm("Createdirections", {}, this.gridApi, "lg");

            } else {
                this.showForm("Create", {}, this.gridApi, "xl");

            }
        },
        newData(data) {
            console.log("voici la nouvelle data", data);
            this.agGridData = data;
            try {
                this.form.userFiltre = data.__allFilters;
            } catch (e) {
            }
        },
        createLine() {
            this.isLoading = true;
            const model = this.gridApi1.getFilterModel();
            console.log("model ===>", model);

            this.form.horaires = this.horairesSelect.join(",");
            this.form.directions = this.directionSelect.join(",");
            this.form.userFiltre = JSON.stringify(this.form.userFiltre);
            this.axios
                .post("/api/modelslistings", this.form)
                .then((response) => {
                    this.isLoading = false;
                    this.resetForm();
                    this.gridApi.applyServerSideTransaction({
                        add: [response.data],
                    });
                    this.gridApi.refreshServerSide();
                    this.$bvModal.hide(this.modalFormId);
                    this.$toast.success("Operation effectuer avec succes");
                    this.$emit("close");
                    console.log(response.data);
                })
                .catch((error) => {
                    this.errors = error.response.data.errors;
                    this.isLoading = false;
                    this.$toast.error(
                        "Erreur survenue lors de l'enregistrement"
                    );
                });
        },
        resetForm() {
            this.form = {
                id: "",
                Libelle: "",
                userFiltre: "",
                userMatricule: "",
                created_at: "",
                updated_at: "",
                extra_attributes: "",
                deleted_at: "",
                identifiants_sadge: "",
            };
        },
        addPoste(data) {

            const clickedDate = data.id;


            const index = this.horairesSelect.indexOf(clickedDate);
            if (index > -1) {
                // Si la date est déjà sélectionnée, la supprimer du tableau
                // this.selectedDates.splice(index, 1);
            } else {
                // Si la date n'est pas déjà sélectionnée, l'ajouter au tableau
                this.horairesSelect.push(clickedDate);
            }

            console.log('this.horairesSelect', this.horairesSelect);
            // this.horairesSelect.push(data.id);
            this.$toast.success("Operation effectuer avec succes");
        },
        addDirection(data) {

            const clickedDate = data.id;


            const index = this.directionSelect.indexOf(clickedDate);
            if (index > -1) {
                // Si la date est déjà sélectionnée, la supprimer du tableau
                // this.selectedDates.splice(index, 1);
            } else {
                // Si la date n'est pas déjà sélectionnée, l'ajouter au tableau
                this.directionSelect.push(clickedDate);
            }
            this.$toast.success("Operation effectuer avec succes");
        },
        deletePoste(data) {
            const clickedDate = data.id;

            const index = this.horairesSelect.indexOf(clickedDate);
            if (index > -1) {
                this.horairesSelect.splice(index, 1);
                this.tableKey++;
                this.$toast.success("Operation effectuer avec succes");
            }
        },
        deleteDirection(data) {
            const clickedDate = data.id;

            const index = this.directionSelect.indexOf(clickedDate);
            if (index > -1) {
                this.directionSelect.splice(index, 1);
                this.tableKey++;
                this.$toast.success("Operation effectuer avec succes");
            }
        },
        finishAddPoste() {
            if (this.horairesSelect.length != this.lastPosteSelectCount) {
                this.lastPosteSelectCount = this.horairesSelect.length;
                this.tableKey++;
            }
            this.$bvModal.hide(this.formId);
        },
        finishAddDirection() {
            if (this.directionSelect.length != this.lastdirectionSelectCount) {
                this.lastdirectionSelectCount = this.directionSelect.length;
                this.tableKey++;
            }
            this.$bvModal.hide(this.formId);
        },
        readhistorique() {
            this.historique = 1
            this.historiquetype = 'postePointeuse'
        },
        fermerhistorique() {

            this.historique = false

        },
        showForm(type, data, gridApi, width = "lg") {
            this.formKey++;
            this.formWidth = width;
            this.formState = type;
            this.formData = data;
            this.formGridApi = gridApi;
            this.$bvModal.show(this.formId);
        },
        getselectedDays() {
            if (this.form.lun === 1) {
                this.selectedDays.push("Lundi");
            }
            if (this.form.mar === 1) {
                this.selectedDays.push("Mardi");
            }
            if (this.form.mer === 1) {
                this.selectedDays.push("Mercredi");
            }
            if (this.form.jeu === 1) {
                this.selectedDays.push("Jeudi");
            }
            if (this.form.ven === 1) {
                this.selectedDays.push("Vendredi");
            }
            if (this.form.sam === 1) {
                this.selectedDays.push("Samedi");
            }
            if (this.form.dim === 1) {
                this.selectedDays.push("Dimanche");
            }
            // console.log("selectedDays ==>", this.selectedDays);
        },
    },

};
</script>
